.foot{
  width: 100%
}
@media screen and (min-width: 900px) {
  .foot{
    width: 50%;
  }
  div.foot {
    margin-left: 200px;
    padding: 1px 16px;
    height: 0px;
    width: 77%;
  }
  
}
@media screen and (min-width: 800px) {
  .foot{
    width: 77%;
    
  }
  div.foot {
    margin-left: 200px;
    padding: 1px 16px;
    height: 0px;
    width: 77%;
  }
}


.text-small {
    font-size: 0.9rem;
    
  }
  
  a {
    color: #fff;
    text-decoration: none;
    transition: all 0.3s;
  }
  
  a:hover, a:focus {
    text-decoration: none;
  }
  
  footer {
    background: #24645d;
    color: #fff;
  }
  .text-muted {
    --bs-text-opacity: 1;
    color: #ffffff!important;
}
